<template>
  <div class="challenge_cont detail_cont">
    <div class="edit">
      <div class="cate_title">
        <img src="/images/challenge_h1.png" alt="챌린지타이틀">
      </div>

      <section class="guide">
        <div>
          <h3>
            <img src="/images/svg/noti.svg" alt="">
            챌린지 작성 가이드</h3>
          <ul>
            <li>· 네이버에 주간일기 챌린지 검색 후, 참여하기 버튼을 눌러 챌린지를 시작</li>
            <li>· 챌린지 카테고리에 한 주에 한 번 일기 작성!</li>
            <li>· 주간일기는 전체공개로 발행 필수!</li>
            <li>· 참여 현황에서 스탬프로 진행 상황을 확인할 수 있습니다.</li>
          </ul>
        </div>
      </section>

      <section class="section1">
        <h3>챌린지 썸네일</h3>
        <input type="file"
               id="file"
               ref="getFile"
               @change="imageUpload"/>
        <img src=""
             v-if="fileList.length"
             ref="preview"
             class="previewImg"
             alt="이미지미리보기">
      </section>

      <section class="section2">
        <h3>제목</h3>
        <input type="text" class="mb10" placeholder="제목을 입력해주세요.">
        <editor
          ref="toastEditor"
          :options="editorOptions"
          height="500px"
          initialEditType="wysiwyg"
          previewStyle="vertical"/>
      </section>

      <section class="section3">
        <select class="main_select"
                ref="mainSelect"
                @change="firstSelect($event)">
          <option v-for="(item, index) in category"
                  :key="index">{{ item.text }}
          </option>
        </select>
        <select class="sub_select"
                ref="subSelect"
                @change="secondSelect($event)"
                :disabled="choiceBoolean">
          <option value="">상세 카테고리 선택</option>
        </select>
        <input type="text" placeholder="상세정보를 입력해주세요." v-model="text03">
        <button class="button_default_fill" @click="addInfo">+ 정보추가</button>
      </section>

      <section class="section4">
        <div v-for="(item, idx) in totalText"
             :key="idx"
             class="info_wrap">
          <div class="info_clothes">
            {{ item }}
            <img src="/images/svg/x_icon.svg"
                 alt="엑스아이콘"
                 class="x_icon"
                 @click="deleteInfo(idx)">
          </div>
        </div>
      </section>

      <section>
        <div class="btn_wrap">
          <router-link to="/challenge">
            <button class="button_default_border mr5">취소</button>
          </router-link>
          <button class="button_default_fill" @click="onSubmit">수정</button>
        </div>
      </section>
    </div>
  </div>
</template>

<script>
import '@toast-ui/editor/dist/toastui-editor.css'
import {Editor} from '@toast-ui/vue-editor';

export default {
  name: "ChallengeEdit",
  components: {
    editor: Editor
  },
  data() {
    return {
      fileList: [],
      editorOptions: {
        hideModeSwitch: true,
      },
      subOption: [],
      choiceBoolean: true,
      category: [
        {value: '', text: '의류 카테고리 선택'},
        {value: 'outer', text: '아우터'},
        {value: 'top', text: '상의'},
        {value: 'bottom', text: '하의'},
        {value: 'ops', text: '원피스'},
        {value: 'acc', text: '악세서리'},
      ],
      secondValue: '',
      totalText: [],
      text01: '',
      text02: '',
      text03: '',
    }
  },
  methods: {
    imageUpload() {
      let file = this.$refs.getFile
      this.fileList = file.files // 선택된 파일의 목록이 FileList 객체 형태로 files 속성에 저장
      let reader = new FileReader()
      reader.readAsDataURL(this.fileList[0]);
      reader.onload = () => {
        //로컬 이미지를 보여주기
        this.$refs.preview.src = reader.result;
        //썸네일 이미지 생성
        let thumbImg = new Image() //drawImage 메서드에 넣기 위해 이미지 객체화
        thumbImg.src = reader.result  //data-url를 이미지 객체에 주입
      }
    },
    firstSelect(e) {
      let subOptions = {
        outer: ['상세 카테고리 선택', '코트', '자켓', '패딩'],
        top: ['상세 카테고리 선택', '티셔츠', '셔츠', '블라우스'],
        bottom: ['상세 카테고리 선택', '바지', '청바지', '스커트'],
        ops: ['상세 카테고리 선택', '미니원피스', '롱원피스'],
        acc: ['상세 카테고리 선택', '목걸이', '귀걸이', '팔찌', '기타'],
      }

      const target = e.target.value
      // 의류 카테고리 선택하면 상세 카테고리 선택 disabled 되게하기
      target === '의류 카테고리 선택' ? this.choiceBoolean = true : this.choiceBoolean = false;
      if (target === '아우터') this.subOption = subOptions.outer
      else if (target === '상의') this.subOption = subOptions.top
      else if (target === '하의') this.subOption = subOptions.bottom

      const subSelect = this.$refs.subSelect
      subSelect.options.length = 0; // 셀렉트박스 초기화
      for (let i = 0; i < this.subOption.length; i++) {
        let option = document.createElement('option');
        option.innerText = this.subOption[i];
        subSelect.append(option);
      }

      this.text01 = e.target.value
    },
    secondSelect(e) {
      this.text02 = e.target.value
    },
    addInfo() {
      let textZip = `${this.text01} [${this.text02}] ${this.text03}`
      return this.totalText.push(textZip)
    },
    onSubmit() {
      const content = this.$refs.toastEditor.invoke("getMarkdown");
      console.log('content', content)
    },
    deleteInfo(idx) {
      if (window.confirm('삭제하시겠습니까?')) {
        this.totalText.splice(idx, 1)
      }
    }

  }
}
</script>

<style scoped>

